import Vue from 'vue'
import Router from 'vue-router'
import Home from './Home.vue'
import Goods from './Goods.vue'
import Chose from './Chose.vue'
import Settle from './Settle.vue'
import Need from './Need.vue'
import Ifo from './Ifo.vue'
import Order from './Order.vue'
import Upgrading from './Upgrading.vue'

Vue.use(Router)

export default new Router({
  mode:"hash",
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/list',
      name: 'list',
      component: Goods
    },
    {
      path: '/chose',
      name: 'chose',
      component: Chose
    },
    {
      path: '/settle',
      name: 'settle',
      component: Settle
    },
    {
      path: '/need',
      name: 'need',
      component: Need
    },
    {
      path: '/ifo',
      name: 'ifo',
      component: Ifo
    },
    {
      path: '/order',
      name: 'order',
      component: Order
    },
    {
      path: '/upgrading',
      name: 'upgrading',
      component: Upgrading
    },
    { 
      path: "*", 
      redirect: "/" 
    }
  ]
})
