<template>
    <div class="nav">
        <div class="logo">
            <img class="img" src="/logo-white.png" @click="change('/')">
        </div>
        <div class="right">
            <div class="option">
                <el-link :underline="false" v-if="loged==true"> <i class="el-icon-bell"></i> </el-link>
                <el-link :underline="false" @click="$router.push('/order')" v-if="loged==true"> <i class="el-icon-tickets"></i> </el-link>
            </div>
            <div class="head">
                <el-popover
                placement="bottom-end"
                width="200"
                trigger="hover">
                    <div class="menu">
                        <div class="top"  v-if="loged==true">
                            <h2>用户名</h2>
                            <div class="tags">
                                <el-tag class="tag" size="mini">标签1</el-tag>
                                <el-tag class="tag" size="mini">标签2</el-tag>
                                <el-tag class="tag" size="mini">标签3</el-tag>
                            </div>
                        </div>
                        <div class="middle"  v-if="loged==true">
                            <el-link :underline="false" @click="$router.push('/ifo')">个人信息</el-link>
                            <el-link :underline="false" @click="$router.push('/need')">我是帮手</el-link>
                        </div>
                        <div class="bottom"  v-if="loged==true">
                            <el-button type="danger" @click="logout()">退出</el-button>
                        </div>
                        <div class="noLog" v-if="loged==false">
                            <el-button type="primary" @click="goLog()">登入</el-button>
                            <el-button @click="goReg()">注册</el-button>
                        </div>
                    </div>
                    <img class="user" src="/josh-howard-6BI7fVlw_BA-unsplash.jpg" slot="reference" v-if="loged==true">
                    <img class="user" src="/undraw_male_avatar_323b.svg" slot="reference" v-if="loged==false">
                </el-popover>
                
            </div>
        </div>
    </div>
</template>

<script>

import { addCookie , searchCookie , deleteCookie } from '../myCookie'
import axios from 'axios'

export default {
    name: 'Nav',
    data () {
        return {
            loged: false,
        }
    },
    methods: {
        logout(){
            deleteCookie("token");
            this.$store.state.loged = false;
            this.$data.loged = false;
            this.$router.push('/');
        },
        goReg(){
            this.$store.state.logReg = "reg";
            this.$router.push('/upgrading');
        },
        goLog(){
            this.$store.state.logReg = "log";
            this.$router.push('/upgrading');
        },
        getToken(){
            var obj = this;
            var token = searchCookie("token");
            console.log("get token!!!!!!!!");
            axios.get('https://easy-mock.com/mock/5d5cb424faa4555ebab63d17/helper/user',{
                params: {
                response: "token",
                token: token,
                }
            })
            .then(function(response){
                console.log(response);
                console.log(response.data.token);
                if(response.data.token == ""){
                    console.log("no token 1");
                    obj.$store.state.loged = false;
                    obj.$data.loged = false;
                }else{
                    addCookie("token",response.data.token);
                    obj.$store.state.loged = true;
                    obj.$data.loged = true;
                }
            })
            .catch(function (error){
                console.log(error);
            });
        },
    },
    mounted() {
        this.getToken();
    },
}
</script>

<style lang="scss">
.nav{
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: space-between;
}
.logo{
    display: flex;
    align-items: center;
    margin-left: 1vw;
}
.img{
    height: 60%;
    cursor: pointer;
}
.right{
    display: flex;
    width: 10%;
    height: 100%;
    justify-content: space-around;
    .option{
        display: flex;
        width: 50%;
        justify-content: space-around;;
        align-items: center;
        i{
            font-size: 2em;
        }
        .el-link{
            color: #eaeac1;
        }
        .el-link:hover{
            color: #ede264;
        }
    }
    .head{
        display: flex;
        width: 40%;
        justify-content: center;
        align-items: center;
        min-width: 6vh;
        .user{
            height: 6vh;
            width: 6vh;
            border-radius: 50%;
            object-fit: cover;
        }
    }
}
.menu{
    display: flex;
    flex-direction: column;
    justify-content: space-between;;
    align-items: center;
    height: 30vh;
    .top{
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 90%;
        height: 40%;
        h2{
            margin: 0;
            font-size: 2em;
            letter-spacing: 0.1em;
            font-weight: 500;
        }
        .tags{
            width: 100%;
            margin-top: 1vh;
            .tag{
                margin-right: 5px;
            }
        }
    }
    .middle{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 30%;
        width: 90%;
        .el-link{
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 1vh 0 1vh 0;
            width: 100%;
        }
        .el-link:hover{
            background-color: rgba(0, 0, 0, 0.2);
        }
    }
    .bottom{
        display: flex;
        height: 30%;
        justify-content: center;
        align-items: center;
    }
}
.noLog{
    display: flex;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
}
</style>