<template>
    <div class="index div">
        <nav class="nav div">
            <v-nav></v-nav>
        </nav>
        <div class="body div">
            <div class="sort div">
                <h3>分类</h3>
                <el-divider></el-divider>
                <el-tree 
                :data="sort" 
                :props="defaultProps"  
                accordion  
                @node-click="handleNodeClick"
                class="tree">
                </el-tree>
            </div>
            <div class="show div">
                <div class="search div">
                    <v-search class="input"></v-search>
                </div>
                <div class="list div" v-if="get">
                     <el-carousel type="card" class="cards" :autoplay=false height="90%" arrow="always" indicator-position="outside">
                        <el-carousel-item v-for="i in listSize" :key="i">
                            <v-good class="good"></v-good>
                        </el-carousel-item>
                    </el-carousel>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import Search from './components/Search'
import Good from './components/Good'
import axios from 'axios'
import Nav from './components/Nav'

export default {
    name: 'Goods',
    data () {
        return {
            listSize: 0,
            get :false,
            sort: [{
                label: '数码软件',
                children: [{
                    label: '办公软件',
                },{
                    label: '功能软件',
                },{
                    label: '手机软件',
                },
                ]
            }, {
                label: '电子硬件',
                children: [{
                    label: '电脑检修',
                }, {
                    label: '手机检修',
                }]
            }, {
                label: '家政清洁',
                children: [{
                    label: '电器清洗',
                }, {
                    label: '室内保洁',
                }, {
                    label: '室外保洁',
                }, {
                    label: '保姆家教',
                }]
            },{
                label: '水电修理',
                children: [{
                    label: '水管类维修',
                }, {
                    label: '电路类维修',
                }]
            },{
                label: '力量不够',
                children: [{
                    label: '搬家',
                }, {
                    label: '搬动',
                }]
            }
            ],
            defaultProps: {
                children: 'children',
                label: 'label'
            }
        }
    },
    components:{
        'v-search':Search,
        'v-good':Good,
        'v-nav':Nav
    },
    beforeMount() {
        this.$data.get = false;
        let html = this;
        axios.get("https://easy-mock.com/mock/5d5cb424faa4555ebab63d17/helper/goodList")
        .then(response=>{
            html.$store.state.goods = response.data;
            html.$data.listSize = response.data.length;
            html.$data.get = true;
        })
        .catch(error=>{
            console.log(error);
        })
       // console.log(this.$store.state.goods);
    },
}
</script>

<style lang="scss" scoped>
.div{
    display: flex;
}
.index{
    display: flex;
    height: 100%;
    flex-direction: column;

}
.nav{
    display: flex;
    position: fixed;
    height: 10vh;
    width: 100%;
    background-color: #3978a3;
    z-index: 2;
}
.body{
    display: flex;
    height: 90vh;
    padding-top: 10vh;
    z-index: 1;
    .sort{
        width: 10%;
        height: 100%;
        flex-direction: column;
        align-items: center;
        border-right: 1px solid #dcdfe6;
        h3{
            margin: 0;
            margin-top: 24px;
            color: #12202b;
            letter-spacing: 0.1em;
            font-size: 2em;
            font-weight: 500;
        }
        .tree{
            align-self: flex-start;
            flex-direction: column;
            background: none;
        }
    }
    .show{
        width: 90%;
        height: 100%;
        flex-direction: column;
        .search{
            height: 10%;
            width: 100%;
            justify-content: center;
            align-items: center;
            .input{
                width: 50%;
                height: 36px;
            }
        }
        .list{
            height: 95%;
            width: 100%;
            justify-content: center;
            align-items: center;
            .cards{
                width: 90%;
                height: 90%;
                margin-top: 24px;
                .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
            }
            .card{
                display: flex;
                justify-content: center;
                align-items: center;
            }
            .good{
                width: 100%;
                height: 100%;
            }
        }
    }
}
</style>