<template>
    <div class="table-body">
        <h2>
            登入
        </h2>
        <el-form label-position="top" label-width="60px" :model="ruleForm" ref="ruleForm" status-icon :rules="rules">
            <el-form-item class="title" label="账号 (邮箱)" prop="account">
                <el-input v-model="ruleForm.account"></el-input>
            </el-form-item>
            <el-form-item class="title" label="密码" prop="password">
                <el-input type="password" v-model="ruleForm.password"></el-input>
            </el-form-item> 
            <el-link type="primary">忘记密码？</el-link>
            <div class="footer">
                <el-button @click="$store.state.logReg = 'reg'">注 册</el-button>
                <el-button type="primary" @click="submitForm('ruleForm')">登 入</el-button>
            </div>
        </el-form>
    </div>
</template>

<script>

import axios from 'axios'
import { addCookie , searchCookie , deleteCookie } from '../myCookie'

export default {
    name: 'Login',
    data () {
        return {
            ruleForm: {
                account: '',
                password: '',
            },
            rules: {
                account: [
                    { required: true, trigger: 'blur', message:"请输入用户名" }
                ],
                password: [
                    { required: true, trigger: 'blur', message:"请输入密码" },       
                ],
            },
            center: true,
        }
    },
    methods: {
        submitForm(formName) {
            var compunent = this;
            axios.get('/user', {
                params: {
                    response : "login",
                    email : this.$refs[formName].model.account,
                    password : this.$refs[formName].model.password
                }
            })
            .then(function (response) {
                console.log(response);
                var num = response.data.status;
                //console.log(response.data.status);
                //console.log(this);
                if(num == "201"){
                    compunent.$refs[formName].$message.error('登入失败');
                    compunent.$refs[formName].resetFields();
                }else if(num == "200"){
                    compunent.$refs[formName].$message({
                        message: '登入成功',
                        type: 'success'
                    });
                    addCookie("token",response.data.token);
                    compunent.$refs[formName].resetFields();
                    compunent.$store.state.loged = true;
                    compunent.$router.push('/');
                }
            })
            .catch(function (error) {
                console.log("error"+error)
                compunent.$refs[formName].$message.error('出错了，错误信息'+error);
            }); 
        }
    }
            
}
</script>

<style lang="scss" scoped>
.table-body{
    height: 50vh;
    width: 20vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    background-color: #eaeac1;
    h2{
        margin: 0;
        align-self: flex-start;
        margin-left: 2vw;
        font-size: 2em;
        font-weight: 500;
        letter-spacing: 0.1em;
        margin-bottom: 3vh
    }
}
.footer{
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
</style>