<template>
<div class="reg-body">
    <h2>
        注册
    </h2>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="reg" label-position="left">
        <el-alert title="用户名已存在" type="error" description="用户名与已存在用户重复，请重试" show-icon class="repeat" v-if="repeated"> </el-alert>
        <el-form-item label="用户名" prop="name">
            <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
            <el-input v-model="ruleForm.email"></el-input>
        </el-form-item>
        <el-form-item label="电话号码" prop="number">
            <el-input v-model="ruleForm.number"></el-input>
        </el-form-item>
        <el-form-item label="真实姓名" prop="real">
            <el-input v-model="ruleForm.real"></el-input>
        </el-form-item>
        <el-form-item label="身份证号" prop="id">
            <el-input v-model="ruleForm.id"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
            <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPass">
            <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button @click="change('/')">取 消</el-button>
            <el-button type="primary" @click="submitForm('ruleForm')">注 册</el-button>
        </el-form-item>
    </el-form>
</div>
</template>

<script>

import axios from 'axios'

export default {
    name: 'Reg',
    data () {
        var checkName = (rule, value, callback) => {
            if (value === '') {
               return callback(new Error('请输入用户名'));
            } else {
            if (this.ruleForm.checkPass !== '') {
                this.$refs.ruleForm.validateField('email');
            }
            callback();
            }
        };
        var checkMail = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入邮箱'));
            } else {
            if (this.ruleForm.checkPass !== '') {
                this.$refs.ruleForm.validateField('pass');
            }
            callback();
            }
        };
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入密码'));
            } else {
            var pattern = /^[\w_]{6,16}$/;
            if(!pattern.test(value)){
                callback(new Error('密码不能包含下划线以外的特殊字符'));
            }
            if (this.ruleForm.checkPass !== '') {
                this.$refs.ruleForm.validateField('checkPass');
            }
            callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请再次输入密码'));
            } else if (value !== this.ruleForm.pass) {
                callback(new Error('两次输入密码不一致!'));
            } else {
                callback();
            }
        };
        return {
            repeated: false,
            ruleForm: {
                name: '',
                email: '',
                pass: '',
                checkPass: '',
                real:'',
                id:'',
                number:''
            },
            rules: {
                name: [
                    { validator: checkName, trigger: 'blur' },
                    { min: 3, max: 10, message: '长度在 3 到 10 个字符', required: true, trigger: 'blur' }
                ],
                email: [
                    { validator: checkMail, trigger: 'blur',required: true },
                    { type:"email" , trigger:'change' , message:'请输入正确的邮箱地址'},
                ],
                pass: [
                    { min: 6, max: 16, message: '密码最短6位，最长16位', trigger: 'blur'},
                    { validator: validatePass, trigger: 'blur',required: true },       
                ],
                checkPass: [
                    { validator: validatePass2, trigger: 'blur',required: true },
                ],
                id: [
                    { min: 17, max: 18, message: '请输入正确身份证号', required: true, trigger: 'blur' }
                ],
                real: [
                    { min: 2, max: 10, message: '请输入正确姓名', required: true, trigger: 'blur' }
                ],
                number: [
                    { min: 11, max: 11, message: '请输入正确手机电话号码', required: true, trigger: 'blur' }
                ],
            }
        }
    },
    methods: {
        submitForm(formName) {
            //console.log(this.$data.repeated);
            //console.log(this.$refs[formName].model);
            //this.$data.repeated = true;
            let compunent = this;
            this.$refs[formName].validate((valid) => {
            if (valid) {
                axios.get('/user', {
                    params: {
                        response: "register",
                        email : this.$refs[formName].model.email,
                        username : this.$refs[formName].model.name,
                        password : this.$refs[formName].model.checkPass,
                        id : this.$refs[formName].model.id,
                        name : this.$refs[formName].model.name,
                        number : this.$refs[formName].model.number,
                    }
                })
                .then(function (response) {
                    var num = response.data.status;
                    //console.log(response.data.status);
                    //console.log(this);
                    if(num == "201"){
                        compunent.$data.repeated = true;
                        compunent.$refs[formName].resetFields();
                        compunent.$refs[formName].$message({
                            message: '该用户已存在',
                            type: 'error'
                        });
                    }else if(num == "200"){
                        compunent.$data.repeated = false;
                        compunent.$refs[formName].$message({
                            message: '注册成功',
                            type: 'success'
                        });
                        compunent.$refs[formName].resetFields();
                        compunent.$store.state.logReg = "log";
                    }else{
                        compunent.$data.repeated = true;
                        compunent.$refs[formName].resetFields();
                        compunent.$refs[formName].$message({
                            message: '出错了，请联系管理员处理',
                            type: 'warning'
                        });
                    }
                })
                .catch(function (error) {
                    //console.log(this);
                    console.log("error"+error);
                    compunent.$refs[formName].$message.error('出错了，错误信息:'+error);
                });
            } else {
                compunent.$refs[formName].$message.error('提交出错，请检查信息内容');
                return false;
            }
            });
            //store.state.regVisible = false;
        },
    },
}
</script>

<style lang="scss" scoped>
.reg-body{
    height: 70vh;
    width: 20vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    background-color: #eaeac1;
    h2{
        margin: 0;
        align-self: flex-start;
        margin-left: 2vw;
        font-size: 2em;
        font-weight: 500;
        letter-spacing: 0.1em;
        margin-bottom: 3vh
    }
}

.repeat{
    margin-bottom: 20px; 
}
</style>