<template>
    <div class="sec">
        <div class="title">
            <span>服务列表</span>
            <h2>几十余种专业服务随时提供</h2>
        </div>
        <div class="list" v-if="get">
            <el-row :gutter="20" v-for="i in 2" :key="i">
                <el-col :span="6" v-for="n in listSize" :key="n"><div class="grid-content bg-purple">
                    <v-work></v-work>    
                </div></el-col>
            </el-row>
        </div>
    </div>
</template>

<script>

import Work from './Work'
import axios from 'axios'

export default {
    name: 'Sec',
    data () {
        return {
            get:false,
            listSize:0,
        }
    },
    components:{
        'v-work':Work,
    },
    beforeMount() {
        this.$data.get = false;
        let html = this;
        axios.get("https://easy-mock.com/mock/5d5cb424faa4555ebab63d17/helper/hot")
        .then(response=>{
            html.$store.state.hotGoods = response.data.goods;
            html.$data.listSize = response.data.goods.length/2;
            html.$data.get = true;
        })
        .catch(error=>{
            console.log(error);
        })
       // console.log(this.$store.state.goods);
    },
}
</script>

<style lang="scss" scoped>
.sec{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.title{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 15vh;
    width: 100%;
    span{
        color: #eaeac1;
        letter-spacing: 0.1em;
        font-size: 1em;
    }
    h2{
        margin: 0;
        color: #eee8a2;
        letter-spacing: 0.1em;
        font-size: 3em;
        font-weight: 500;
    }
}
.list{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 70vh;
    width: 100%;
    .el-row{
        width: 80%;
        margin: 1vh 1vw 1vh 1vw;
    }
    .bg-purple {
        background: #4186a7;
    }
    .grid-content {
        height: 30vh;
    }
}
</style>